.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 1rem;
}
@media screen and (min-width: 1920px) {
  .container {
    max-width: 1920px;
  }
}
@media screen and (min-width: 1366px) {
  .container {
    max-width: 1366px;
  }
}
@media screen and (min-width: 760px) {
  .container {
    max-width: 760px;
  }
}

