.header {
  width: 100%;
  height: 5rem;
  background-color: #4595DE;
}

.logo {
  display: flex;
  align-items: center;
  height: 5rem;
  text-transform: lowercase;
}
.logo span {
  font-size: 2.25rem;
  font-weight: 400;
  color: #DAF3FF;
}

.nav {
  border: 1px solid black;
}
.nav ul {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  list-style: none;
  margin-top: 0;
  height: 100%;
  border: 1px solid red;
}
.nav ul li, .nav ul a {
  border: 1px solid yellowgreen;
  font-size: 1.25rem;
  font-weight: 400;
  color: #DAF3FF;
}